___CSS_LOADER_EXPORT___.push([module.id, `/* Editor Container */
.ProseMirror {
  outline: none;
  min-height: 150px;
  padding: 1rem;
  border: 1px solid #e2e8f0;
  border-radius: 0.5rem;
  background-color: #ffffff;
  font-family: 'Inter', -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif;
  font-size: 1rem;
  line-height: 1.6;
  color: #1e293b;
  transition: border-color 0.2s;
}

.ProseMirror:focus {
  border-color: #3b82f6;
  box-shadow: 0 0 0 3px rgba(59, 130, 246, 0.1);
}

/* Placeholder */
.ProseMirror p.is-editor-empty:first-child::before {
  content: attr(data-placeholder);
  float: left;
  color: #94a3b8;
  pointer-events: none;
  height: 0;
}

/* Mention Chip */
.mention {
  background-color: #e0f2fe;
  color: #0369a1;
  border: 1px solid #bae6fd;
  border-radius: 0.25rem;
  padding: 0.1rem 0.3rem;
  font-weight: 500;
  box-decoration-break: clone;
}

/* Suggestion Dropdown */
.items {
  padding: 0.5rem;
  position: relative;
  background: #ffffff;
  border-radius: 0.5rem;
  color: #1e293b;
  overflow: hidden;
  font-size: 0.9rem;
  box-shadow: 
    0 4px 6px -1px rgba(0, 0, 0, 0.1), 
    0 2px 4px -1px rgba(0, 0, 0, 0.06),
    0 0 0 1px rgba(0, 0, 0, 0.05);
  min-width: 150px;
}

/* Dropdown Item */
.item {
  display: block;
  width: 100%;
  text-align: left;
  background: transparent;
  border: none;
  border-radius: 0.25rem;
  padding: 0.4rem 0.6rem;
  margin-bottom: 0.1rem;
  cursor: pointer;
  color: #475569;
  font-weight: 500;
  transition: all 0.1s;
}

.item:last-child {
  margin-bottom: 0;
}

.item:hover {
  background-color: #f1f5f9;
  color: #0f172a;
}

.item.is-selected {
  background-color: #eff6ff;
  color: #2563eb;
}`, "",{"version":3,"sources":["webpack://./src/lib/components/DashTiptap.css"],"names":[],"mappings":"AAAA,qBAAqB;AACrB;EACE,aAAa;EACb,iBAAiB;EACjB,aAAa;EACb,yBAAyB;EACzB,qBAAqB;EACrB,yBAAyB;EACzB,yGAAyG;EACzG,eAAe;EACf,gBAAgB;EAChB,cAAc;EACd,6BAA6B;AAC/B;;AAEA;EACE,qBAAqB;EACrB,6CAA6C;AAC/C;;AAEA,gBAAgB;AAChB;EACE,+BAA+B;EAC/B,WAAW;EACX,cAAc;EACd,oBAAoB;EACpB,SAAS;AACX;;AAEA,iBAAiB;AACjB;EACE,yBAAyB;EACzB,cAAc;EACd,yBAAyB;EACzB,sBAAsB;EACtB,sBAAsB;EACtB,gBAAgB;EAChB,2BAA2B;AAC7B;;AAEA,wBAAwB;AACxB;EACE,eAAe;EACf,kBAAkB;EAClB,mBAAmB;EACnB,qBAAqB;EACrB,cAAc;EACd,gBAAgB;EAChB,iBAAiB;EACjB;;;iCAG+B;EAC/B,gBAAgB;AAClB;;AAEA,kBAAkB;AAClB;EACE,cAAc;EACd,WAAW;EACX,gBAAgB;EAChB,uBAAuB;EACvB,YAAY;EACZ,sBAAsB;EACtB,sBAAsB;EACtB,qBAAqB;EACrB,eAAe;EACf,cAAc;EACd,gBAAgB;EAChB,oBAAoB;AACtB;;AAEA;EACE,gBAAgB;AAClB;;AAEA;EACE,yBAAyB;EACzB,cAAc;AAChB;;AAEA;EACE,yBAAyB;EACzB,cAAc;AAChB","sourcesContent":["/* Editor Container */\r\n.ProseMirror {\r\n  outline: none;\r\n  min-height: 150px;\r\n  padding: 1rem;\r\n  border: 1px solid #e2e8f0;\r\n  border-radius: 0.5rem;\r\n  background-color: #ffffff;\r\n  font-family: 'Inter', -apple-system, BlinkMacSystemFont, \"Segoe UI\", Roboto, Helvetica, Arial, sans-serif;\r\n  font-size: 1rem;\r\n  line-height: 1.6;\r\n  color: #1e293b;\r\n  transition: border-color 0.2s;\r\n}\r\n\r\n.ProseMirror:focus {\r\n  border-color: #3b82f6;\r\n  box-shadow: 0 0 0 3px rgba(59, 130, 246, 0.1);\r\n}\r\n\r\n/* Placeholder */\r\n.ProseMirror p.is-editor-empty:first-child::before {\r\n  content: attr(data-placeholder);\r\n  float: left;\r\n  color: #94a3b8;\r\n  pointer-events: none;\r\n  height: 0;\r\n}\r\n\r\n/* Mention Chip */\r\n.mention {\r\n  background-color: #e0f2fe;\r\n  color: #0369a1;\r\n  border: 1px solid #bae6fd;\r\n  border-radius: 0.25rem;\r\n  padding: 0.1rem 0.3rem;\r\n  font-weight: 500;\r\n  box-decoration-break: clone;\r\n}\r\n\r\n/* Suggestion Dropdown */\r\n.items {\r\n  padding: 0.5rem;\r\n  position: relative;\r\n  background: #ffffff